<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Requisitos legales</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ link.name }}</div>

          <div style="white-space: pre-wrap" v-html="link.description"></div>

          <div class="text-center mt-5">
            <v-btn color="primary" width="240" @click="goToLawyer">Solicita tu abogado</v-btn>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'RequirementsDetail',
  data: () => ({
    category: '',
    subcategory: '',
    link: '',
    items: []
  }),
  computed: {
    ...mapGetters(['user', 'services']),
    service() {
      let type = this.$route.params.type
      return this.services.find(service => service.type === type)
    }
  },
  mounted() {
    this.loadDocuments()
  },
  methods: {
    goToLawyer() {
      this.$router.push({name: 'MyLawyer'})
    },
    loadDocuments() {
      let url = 'categories?type=requirement'
      axios.get(url).then((response) => {
        let id = this.$route.params.id
        let id2 = this.$route.params.id2
        let id3 = this.$route.params.id3

        this.category = response.data.find(obj => parseInt(obj.id) === parseInt(id))
        this.subcategory = this.category.subcategories.find(obj => parseInt(obj.id) === parseInt(id2))
        this.link = this.subcategory.links.find(obj => parseInt(obj.id) === parseInt(id3))
      })
    }
  }
}
</script>
